<template>
  <div>
    <vs-card class="p-5">
      <router-link to="/account-admins">← Back</router-link>
      <h4 class="inline-block ml-2">Create Admin Account</h4>
      <div class="contained-example-container">
        <form class="mt-4 vs-con-loading__container" id="div-with-loading">
          <div class="vx-row">
            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="First Name"
                v-model="admin.firstName"
                name="input1"
                class="w-full mt-5"
              />
            </div>
            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Last Name"
                v-model="admin.lastName"
                name="input1"
                class="w-full mt-5"
              />
            </div>
            <div class="vx-col w-1/2 pt-3">
              <label>Type</label>
              <v-select class="mt-1" v-model="admin.type" label="name" :options="adminType" />
            </div>

            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Country Code"
                v-model="admin.countryCode"
                name="input1"
                class="w-full mt-5"
              />
            </div>

            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Phone Number"
                v-model="admin.phoneNumber"
                name="input1"
                class="w-full mt-5"
              />
            </div>
            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Email"
                v-model="admin.email"
                name="input1"
                type="email"
                class="w-full mt-5"
              />
            </div>
            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label="Profile Image"
                accept="image/png, image/jpeg, image/jpg"
                @change="handleFileChange($event)"
                name="input1"
                type="file"
                class="w-full mt-0"
              />
            </div>

            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Password"
                v-model="admin.password"
                type="password"
                name="input1"
                class="w-full mt-5"
              />
            </div>

            <div class="vx-col w-1/2 pt-3">
              <vs-input
                label-placeholder="Confirm Password"
                v-model="admin.confirmPassword"
                name="input1"
                type="password"
                class="w-full mt-5"
              />
            </div>
          </div>
          <vs-button type="filled" @click.prevent="submitForm" class="mt-5 block">Create Account</vs-button>
        </form>
      </div>
    </vs-card>
  </div>
</template>

<script>
import vSelect from "vue-select";
export default {
  components: {
    "v-select": vSelect
  },
  data() {
    return {
      type: [],
      states: [],
      adminType: ["admin", "accounting", "customerCare"],
      admin: {
        firstName: "",
        type: "",
        lastName: "",
        countryCode: "",
        phoneNumber: "",
        email: "",
        img: "",
        password: "",
        confirmPassword: ""
      }
    };
  },
  mounted() {},
  methods: {
    openLoadingInDiv() {
      this.$vs.loading({
        container: "#div-with-loading",
        scale: 0.6
      });
    },
    handleFileChange: function(event) {
      console.log(event.target.files[0]);
      this.admin.img = event.target.files[0];
    },
    submitForm() {
      // this.admin.State = this.admin.State.name;
      // this.admin.countryId = this.admin.countryId._id;
      this.openLoadingInDiv();
      let admin = this.admin;
      console.log(admin);
      this.$store
        .dispatch("createadmin", admin)
        .then(resp => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");
          this.$vs.notify({
            title: "Create Admin Account",
            text: "Admin Account created successfully!",
            color: "success"
          });
          this.admin = {
            firstName: "",
            type: "",
            lastName: "",
            countryCode: "",
            phoneNumber: "",
            email: "",
            img: "",
            password: "",
            confirmPassword: ""
          };
          //   console.log(resp);
        })
        .catch(err => {
          this.$vs.loading.close("#div-with-loading > .con-vs-loading");

          if (err.response) {
            if (err.response.status == 400) {
              console.log(err.response.data);
              this.$vs.notify({
                title: "Create admin Account",
                text: err.response.data,
                color: "warning"
              });
            } else {
              this.$vs.notify({
                title: "Create admin Account",
                text: "Unable to create admin account, try again!",
                color: "warning"
              });
            }
          } else {
            this.$vs.notify({
              title: "Create admin Account",
              text: "Unable to create admin account, try again!",
              color: "warning"
            });
          }
        });
    }
  }
};
</script>
